'use client'

import { useState } from 'react'
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle } from '@/components/ui/dialog'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Textarea } from '@/components/ui/textarea'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Checkbox } from '@/components/ui/checkbox'
import { toast } from 'sonner'
import { useAuthStore } from '@/lib/store/auth-store'
import type { PostCategory } from '@/lib/mock/board'

interface CreatePostDialogProps {
  open: boolean
  onOpenChange: (open: boolean) => void
  onSuccess?: () => void
}

const CATEGORY_OPTIONS: { value: PostCategory; label: string; adminOnly?: boolean }[] = [
  { value: 'notice', label: 'Í≥µÏßÄÏÇ¨Ìï≠', adminOnly: true },
  { value: 'general', label: 'ÏûêÏú†Í≤åÏãúÌåê' },
  { value: 'question', label: 'ÏßàÎ¨∏' },
  { value: 'tips', label: 'ÌåÅ/ÎÖ∏ÌïòÏö∞' },
  { value: 'event', label: 'Ïù¥Î≤§Ìä∏', adminOnly: true },
]

export function CreatePostDialog({
  open,
  onOpenChange,
  onSuccess,
}: CreatePostDialogProps) {
  const { user } = useAuthStore()
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [formData, setFormData] = useState({
    category: 'general' as PostCategory,
    title: '',
    content: '',
    isPinned: false,
  })

  const isAdmin = user?.role === 'admin'

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()

    // Validation
    if (!formData.title.trim()) {
      toast.error('Ï†úÎ™©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.')
      return
    }

    if (!formData.content.trim()) {
      toast.error('ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.')
      return
    }

    // Check admin-only categories
    if (!isAdmin && (formData.category === 'notice' || formData.category === 'event')) {
      toast.error('Ìï¥Îãπ Ïπ¥ÌÖåÍ≥†Î¶¨Îäî Í¥ÄÎ¶¨ÏûêÎßå ÏÇ¨Ïö©Ìï† Ïàò ÏûàÏäµÎãàÎã§.')
      return
    }

    setIsSubmitting(true)

    try {
      // TODO: Ïã§Ï†ú API Ìò∏Ï∂úÎ°ú Î≥ÄÍ≤Ω
      // await apiService.post('/posts', formData)

      // Mock: Simulate API call
      await new Promise(resolve => setTimeout(resolve, 1000))

      toast.success('Í≤åÏãúÍ∏ÄÏù¥ Îì±Î°ùÎêòÏóàÏäµÎãàÎã§.')

      // Reset form
      setFormData({
        category: 'general',
        title: '',
        content: '',
        isPinned: false,
      })

      onOpenChange(false)
      onSuccess?.()

    } catch (error) {
      toast.error('Í≤åÏãúÍ∏Ä Îì±Î°ùÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.')
      console.error('Failed to create post:', error)
    } finally {
      setIsSubmitting(false)
    }
  }

  const handleChange = (field: string, value: any) => {
    setFormData(prev => ({ ...prev, [field]: value }))
  }

  // Filter categories based on user role
  const availableCategories = CATEGORY_OPTIONS.filter(
    option => isAdmin || !option.adminOnly
  )

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="max-w-3xl max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle>ÏÉà Í∏Ä ÏûëÏÑ±</DialogTitle>
          <DialogDescription>
            ÌåÄÏõêÎì§Í≥º Í≥µÏú†Ìï† ÎÇ¥Ïö©ÏùÑ ÏûëÏÑ±ÌïòÏÑ∏Ïöî.
          </DialogDescription>
        </DialogHeader>

        <form onSubmit={handleSubmit} className="space-y-6">
          {/* Category */}
          <div className="space-y-2">
            <Label htmlFor="category">
              Ïπ¥ÌÖåÍ≥†Î¶¨ <span className="text-red-500">*</span>
            </Label>
            <Select
              value={formData.category}
              onValueChange={(value) => handleChange('category', value)}
            >
              <SelectTrigger id="category">
                <SelectValue />
              </SelectTrigger>
              <SelectContent>
                {availableCategories.map((option) => (
                  <SelectItem key={option.value} value={option.value}>
                    {option.label}
                    {option.adminOnly && ' (Í¥ÄÎ¶¨Ïûê Ï†ÑÏö©)'}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>

          {/* Title */}
          <div className="space-y-2">
            <Label htmlFor="title">
              Ï†úÎ™© <span className="text-red-500">*</span>
            </Label>
            <Input
              id="title"
              value={formData.title}
              onChange={(e) => handleChange('title', e.target.value)}
              placeholder="Ï†úÎ™©ÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
              required
            />
          </div>

          {/* Content */}
          <div className="space-y-2">
            <Label htmlFor="content">
              ÎÇ¥Ïö© <span className="text-red-500">*</span>
            </Label>
            <Textarea
              id="content"
              value={formData.content}
              onChange={(e) => handleChange('content', e.target.value)}
              placeholder="ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî&#10;&#10;ÌåÅ: Shift+EnterÎ°ú Ï§ÑÎ∞îÍøàÌï† Ïàò ÏûàÏäµÎãàÎã§."
              rows={12}
              required
            />
            <p className="text-xs text-muted-foreground">
              Markdown ÌòïÏãùÏùÑ ÏßÄÏõêÌï©ÎãàÎã§ (Ìñ•ÌõÑ ÏóÖÎç∞Ïù¥Ìä∏ ÏòàÏ†ï)
            </p>
          </div>

          {/* Pin option (Admin only) */}
          {isAdmin && (
            <div className="flex items-center space-x-2">
              <Checkbox
                id="isPinned"
                checked={formData.isPinned}
                onCheckedChange={(checked) => handleChange('isPinned', checked)}
              />
              <Label
                htmlFor="isPinned"
                className="text-sm font-normal cursor-pointer"
              >
                ÏÉÅÎã® Í≥†Ï†ï (Í≥µÏßÄÏÇ¨Ìï≠)
              </Label>
            </div>
          )}

          <DialogFooter>
            <Button
              type="button"
              variant="outline"
              onClick={() => onOpenChange(false)}
              disabled={isSubmitting}
            >
              Ï∑®ÏÜå
            </Button>
            <Button type="submit" disabled={isSubmitting}>
              {isSubmitting ? 'Îì±Î°ù Ï§ë...' : 'Í≤åÏãúÍ∏Ä Îì±Î°ù'}
            </Button>
          </DialogFooter>
        </form>
      </DialogContent>
    </Dialog>
  )
}

